@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Metal+Mania&family=Roboto+Mono:ital,wght@0,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header-text {
    font-family: 'Roboto Mono';
    font-size: 2rem;
}

.header-subtext {
    font-family: "Roboto Mono";
    font-size: 1.5rem;
}

.header-wrapper {
    /* margin-bottom: 20px; */
    padding-top: 20px;
}
.dice-image {
    height: 300px;
}

.dice-image:hover {
    cursor:pointer
}

.dice-number {
    font-family: "Metal Mania";
    font-size: 6rem;
    position: absolute;
    top: 32%;
    left: 48.5%;
}

.dice-number:hover {
    cursor:pointer
}

.dice-wrapper {
    margin: 50px 0;
    position: relative
}


.display-wrapper {
    font-family: 'Roboto Mono'
}

.display-preface {
    font-size: 1.5rem;
    margin: 10px 0;
}

.display-title {
    font-size: 2rem ;
    margin: 10px 10px;
}

.display-summary {
    margin: 0 30%
}
